export default function PresentationStep2(
  { browser }: { browser?: BrowserType },
) {
  const i18n = useI18n<LangType>(langs);

  const [imgSrc, setImgSrc] = useState("");
  const isNewIosStart = browser === "ios";
  useEffect(() => {
    const lang = getBrowserLang();
    const isIosSafari = isSafari() && isMobile();
    const isMacSafari = isSafari() && !isMobile();
    let gif = `assets/step2_${lang}.gif`;
    if (isIosSafari) {
      gif = `assets/step2_safari_ios.gif`;
    } else if (isMacSafari) {
      gif = `assets/step2_safari_mac.gif`;
    }
    setImgSrc(gif);
  }, []);
  return (
    <div className={classNames(styles.container)}>
      <style jsx>{incssStyles}</style>
      <div
        className={classNames(
          "notranslate",
          styles.title,
          "flex flex-col md:flex-row items-center text-base w-full",
        )}
      >
        <div className={classNames("w-full")}>
          <IOS hidden={browser !== "ios-safari" && browser !== "ios"} />
          <MacOS hidden={browser !== "mac-safari"} />
          <Other hidden={!!browser} />
        </div>
        {!isNewIosStart && (
          <div className={classNames(styles["img-wrapper"])}>
            <Image
              loading={"lazy"}
              src={imgSrc}
              hidden={!imgSrc}
              fill={true}
              style={{ objectFit: "contain" }}
              alt=""
            />
          </div>
        )}
      </div>

      <div className={classNames("browser-container")}>
        <div className={classNames("row")}>
          <div className={classNames("column left")}>
            <span
              className={classNames("dot")}
              style={{ background: "#ED594A" }}
            >
            </span>
            <span
              className={classNames("dot")}
              style={{ background: "#FDD800" }}
            >
            </span>
            <span
              className={classNames("dot")}
              style={{ background: "#5AC05A" }}
            >
            </span>
          </div>
          <div className={classNames("column middle")}>
            <input
              type="text"
              disabled
              value="https://immersivetranslate.com"
            />
          </div>
          <div className={classNames("column right")}>
            <div style={{ float: "right" }}>
              <span className={classNames("bar")}></span>
              <span className={classNames("bar")}></span>
              <span className={classNames("bar")}></span>
            </div>
          </div>
        </div>

        <div className={classNames(styles.content)} dangerouslySetInnerHTML={{ __html: i18n["translateContent"] }}>
        </div>
      </div>
    </div>
  );
}

function IOS({ hidden }: { hidden: boolean }) {
  const i18n = useI18n<LangType>(langs);
  return (
    <div
      className={classNames("notranslate", styles.title, "text-sm")}
      hidden={hidden}
    >
      <div className={classNames("text-sm")} dangerouslySetInnerHTML={{ __html: i18n["iOS"]["title"] }}>
      </div>
      <div className={classNames("mt-2", "text-sm")}>
        {i18n["iOS"]["subtitle"]}
      </div>
    </div>
  );
}

